/* Contact
   ========================================================================== */

/* Styles for icons in Contact */
.contact-icons {
  font-size: 1rem;
  padding-bottom: 1em;
  text-align: center;

  /* Increase font size on medium and up devices */
  @include breakpoint($mq_medium) {
    font-size: 1.25rem;
  }

  /* Styles for all but first icon */
  :not(:first-child) {
    margin-left: 10%;

    /* Decrease margin on large and up devices */
    @include breakpoint($mq_large) {
      margin-left: 5%;
    }
  }

  /* Styles for hover over an icon (change color) */
  span:hover {
    color: $middle_gray;

    .touch & {
      color: $body_gray;
    }
  }
}
